:root {
  --main-accent: #060b35;
  --secondary: #616070;
  --blue-accent: #00acdc;
  --gray-accent: #f3f3f3;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid var(--gray-accent);
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: var(--main-accent);
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__number {
  color: var(--secondary);
  align-self: start;
  margin-top: 4px;
  margin-bottom: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  color: var(--main-accent);
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.card__price-text {
  color: var(--secondary);
  font-size: 12px;
  line-height: 18px;
}

.card__price-number {
  color: var(--main-accent);
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background: var(--blue-accent);
  border: none;
  border-radius: 5px;
  flex-shrink: 0;
  width: 100%;
  padding: 12px 69px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  border: 1px solid var(--blue-accent);
  color: var(--blue-accent);
  background: #fff;
}

.card__rating {
  flex-shrink: 0;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  width: 100%;
  margin: 24px 0 16px;
  display: flex;
}

